import React from "react";
import Img1 from "../static/anhtheanh.jpg";
import Img2 from "../static/chiphuong.jpg";
import Img3 from "../static/anhduong.jpg";
import Img4 from "../static/anhmarkus.jpg";
import Img5 from "../static/chihuong.jpg";
import Img6 from "../static/anhphu.jpg";
import Img7 from "../static/anhphi.jpg";

import { AnimationOnScroll } from "react-animation-on-scroll";

export default function Team() {
  const members = [
    { img: Img1, name: "Th Anh", role: "Founder" },
    { img: Img2, name: "Qunh Phng", role: "Manager" },
    { img: Img3, name: "Dng Ocean", role: "Admin" },
    { img: Img4, name: "Markus", role: "Analyst" },
    { img: Img5, name: "Hng V", role: "Researcher" },
    { img: Img6, name: "Nguyn Hng Thin Ph", role: "Admin" },
    { img: Img7, name: "Phi Nguyn", role: "Researcher" },
  ];

  return (
    <div className="bg-[#2B076E] pt-1 xl:pt-10">
      <AnimationOnScroll animateIn="animate__zoomIn" delay={500}>
        <div className="w-[90%] sm:w-[80%] md:w-[40%] mx-auto text-center text-white text-xl py-6 sm:text-3xl">
          i ng CK Capital
        </div>
      </AnimationOnScroll>

      <div className="flex gap-8 overflow-x-auto py-10 px-4 sm:grid sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 sm:overflow-x-visible">
        {members.map((member, index) => (
          <AnimationOnScroll animateIn="animate__fadeInUp" delay={600 + index * 100} key={index}>
            <div className="flex-shrink-0 w-64 text-center mx-auto">
              <div className="w-32 h-32 mx-auto rounded-full border-4 border-white overflow-hidden">
                <img src={member.img} alt={member.name} className="w-full h-full object-cover" />
              </div>
              <h3 className="text-white text-lg mt-4">{member.name}</h3>
              <p className="text-gray-400">{member.role}</p>
            </div>
          </AnimationOnScroll>
        ))}
      </div>
    </div>
  );
}
