import React, { useState } from "react";
import Web3 from "web3";
import logo from "../static/logo.png";
import "./NavBar.css";

export default function NavBar() {
  const [isMenuClosed, setMenuClosed] = useState(true);
  const [address, setAddress] = useState("");
  const links = ["Sn phm", "Tnh nng", "Thng tin", "Lin h"];

  async function connectWallet() {
    if (window.ethereum) {
      const web3 = new Web3(window.ethereum);
      try {
        const accounts = await window.ethereum.request({ method: "eth_requestAccounts" });
        setAddress(accounts[0]);
      } catch (error) {
        console.error(error);
      }
    } else {
      alert("No Ethereum provider found.");
    }
  }

  return (
    <div className="bg-[#0D0D2B] text-white flex font-Rubik">
      <nav className="w-[100%] sm:w-[90%] ml-auto mr-auto">
        <div className="text-lg bold w-[15%] ">
          <img src={logo} alt="" />
        </div>
        <div
          className="nav-hamburguer-icon"
          onClick={() => setMenuClosed(!isMenuClosed)}
        >
          
        </div>
        <div className="nav-links">
          {links.map((node) => {
            return (
              <a
                key={node}
                href="/"
                className="text-white text-base py-2 px-4 hover:border-b-[1px] hover:border-blue-500"
              >
                {node}
              </a>
            );
          })}
          <button
            onClick={connectWallet}
            className="bg-blue-500 text-white py-2 px-10 rounded-full mb-0 mt-0"
          >
            {address ? address.slice(0, 6) + "..." + address.slice(-4) : "Kt ni v"}
          </button>
        </div>
      </nav>
      <div className={`overlay-container${isMenuClosed ? "-hidden" : "-show"}`}>
        <div style={{ backgroundColor: "#252540" }}>
          <div
            className="overlay-buttonClose"
            onClick={() => setMenuClosed(!isMenuClosed)}
          >
            <span>&times;</span>
          </div>
          <div className="overlay-menu-wrapper">
            <div className="overlay-menu">
              {links.map((node) => {
                return (
                  <a
                    key={node}
                    onClick={() => setMenuClosed(!isMenuClosed)}
                    className="overlay-menu-item"
                    style={{ color: "white" }}
                  >
                    {node}
                  </a>
                );
              })}
              <div>
                <button
                  onClick={connectWallet}
                  className="bg-blue-500 text-white py-2 px-4 rounded-full mb-0 mt-0"
                >
                  {address ? address.slice(0, 6) + "..." + address.slice(-4) : "Kt ni v"}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
